import React, { useEffect, useRef, useState } from "react";
import Footer from "../footer/Footer";
import "./Styles.css";

const Src3 = ({ setIsload }) => {
  const changeload = () => {
    setIsload(true);
  };

  return (
    <video onLoadedData={changeload} autoPlay playsInline type="video/mp4" muted loop>
      <source className="videoInactive" src={`/videos/video-soon.mp4`}></source>
    </video>
  );
};

const Video = () => {
  const mask = useRef();
  const titleRef = useRef();
  const [isLoad, setIsload] = useState(false);

  return (
    <>
      <section className="main">
        {!isLoad && (
          <div className="loading">
            <p>Loading</p>
          </div>
        )}
        <div>{<Src3 setIsload={setIsload} />}</div>
        <img ref={mask} src="/videos/mask-lhc.png" alt="mask" className="mask" />
        {/*<h2 ref={titleRef}>LHC</h2>*/}
      </section>
      <footer>
        <Footer />
      </footer>
    </>
  );
};

export default Video;
